import { NgModule } from '@angular/core';
import { StoreModule } from '@ngrx/store';
import { StoreDevtoolsModule } from '@ngrx/store-devtools';
import { environment } from '@src/environments/environment';
import { EntityDataModule } from '@ngrx/data';
import { entityConfig } from './entity-metadata';
import { EffectsModule } from '@ngrx/effects';
import { StoreState } from './store.state';
import { reducer as getConfigReducer } from './reducer.config';
import { AppStoreService } from './store.service';
import { UserService } from './services/user.service';
import { ServiceService } from './services/service.service';
import { MessageService } from './services/message.service';
import { ProductService } from './services/product.service';
import { TaskService } from './services/task.service';
import { InfoService } from './services/info.service';

@NgModule({
  imports: [
    StoreModule.forRoot<StoreState>({
      config: getConfigReducer(),
    }),
    StoreDevtoolsModule.instrument({
      maxAge: 25,
      logOnly: environment.production,
    }),
    EffectsModule.forRoot([]),
    EntityDataModule.forRoot(entityConfig),
  ],
  exports: [StoreModule, StoreDevtoolsModule, EntityDataModule, EffectsModule],
  providers: [
    AppStoreService,
    // Entity services
    UserService,
    ServiceService,
    MessageService,
    ProductService,
    TaskService,
    InfoService,
  ],
})
export class AppStoreModule {}
