import {
  Component,
  OnInit,
  Input,
  AfterViewInit,
  OnDestroy,
} from '@angular/core';

import { IconType } from 'ng-icon-type';
import { Subscription } from 'rxjs';
import { AeTextContentComponent } from '../ae-text-content/ae-text-content.component';
import { AeComponentView } from './ae-component-view.class';
import { AeComponentViewService } from './ae-component-view.service';

@Component({
  selector: 'ae-component-view',
  templateUrl: './ae-component-view.component.html',
  styleUrls: ['./ae-component-view.component.scss'],
})
export class AeComponentViewComponent implements OnInit, OnDestroy {
  private subscriptions!: Subscription[];
  data: Partial<AeComponentView> = {};

  openCodeIcon: IconType = 'code';
  copyIcon: IconType = 'content_copy';
  contentVisible = false;

  constructor(public service: AeComponentViewService) {}

  ngOnInit(): void {
    this.subscriptions = [
      this.service.data$.subscribe((data) => {
        this.data = data;
      }),
    ];
  }

  ngOnDestroy(): void {
    for (let s of this.subscriptions) {
      s.unsubscribe();
    }
  }
}
