import { Injectable } from '@angular/core';
import { Subject } from 'rxjs';
import { ButtonLike } from './types';

@Injectable({
  providedIn: 'root',
})
export class AeNavigationService {
  // public
  public readonly style$: Subject<CSSStyleDeclaration> = new Subject<CSSStyleDeclaration>();
  public readonly menu$: Subject<Partial<ButtonLike>[]> = new Subject<
    Partial<ButtonLike>[]
  >();

  public readonly toolbar$: Subject<Partial<ButtonLike>[]> = new Subject<
    Partial<ButtonLike>[]
  >();

  /**
   * @description Set menu
   * @param menuItems
   */
  public menu(menuItems: Partial<ButtonLike>[]) {
    setTimeout(() => {
      this.menu$.next(menuItems);
    });
  }
  /**
   * @description Set style
   * @param style
   */
  public style(style: CSSStyleDeclaration) {
    setTimeout(() => {
      this.style$.next(style);
    });
  }
  /**
   * @description Set toolbar
   * @param toolbarItems
   */
  public toolbar(toolbarItems: Partial<ButtonLike>[]) {
    setTimeout(() => {
      this.toolbar$.next(toolbarItems);
    });
  }
}
