import { createAction, createReducer, on, props } from '@ngrx/store';

export interface ConfigState {
  name: string;
  logo: string;
  theme: string;
}

const initialconfig: Partial<ConfigState> = {};

const name = createAction('[CONFIG] SET NAME', props<{ name: string }>());
const logo = createAction('[CONFIG] SET LOGO', props<{ logo: string }>());
const theme = createAction('[CONFIG] SET THEME', props<{ theme: string }>());

export const reducer = () =>
  createReducer(
    initialconfig,
    on(name, (state, action) => ({ ...state, ...action })),
    on(logo, (state, action) => ({ ...state, ...action })),
    on(theme, (state, action) => ({ ...state, ...action }))
  );

export const actions = () => ({ name, logo, theme });
