import { Injectable } from '@angular/core';
import { IComponentService } from 'projects/ae-common/src/lib';
import { Subject, Subscription } from 'rxjs';
import { AeComponentViewService } from './ae-component-view/ae-component-view.service';
import { AeDoc } from './ae-doc.class';

@Injectable({
  providedIn: 'root',
})
export class AeDocService implements IComponentService<AeDoc> {
  public subscriptions: Subscription[] = [];
  public snapshot: Partial<AeDoc> = {};
  public data$ = new Subject<Partial<AeDoc>>();

  constructor(private viewService: AeComponentViewService) {}

  public next(data: Partial<AeDoc>) {
    setTimeout(() => {
      this.data$.next(data);
      if (data.code) this.viewService.next(data.code);
    });
  }
}
