import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { Subscription } from 'rxjs';
import { AeDoc } from './ae-doc.class';
import { AeDocService } from './ae-doc.service';

@Component({
  selector: 'ae-doc',
  templateUrl: './ae-doc.component.html',
  styleUrls: ['./doc.style.scss'],
})
export class AeDocComponent implements OnInit, OnDestroy {
  private subscriptions: Subscription[] = [];
  data: Partial<AeDoc> = {};

  constructor(public service: AeDocService) {}

  ngOnDestroy(): void {
    for (let s of this.subscriptions) {
      s.unsubscribe();
    }
  }
  ngOnInit(): void {
    this.subscriptions = [
      this.service.data$.subscribe((data) => {
        this.data = data;
      }),
    ];
  }
}
