import { BreakpointObserver, Breakpoints } from '@angular/cdk/layout';
import { ChangeDetectionStrategy, Component } from '@angular/core';
import { IconType } from 'ng-icon-type';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { AeNavigationService } from './ae-navigation.service';

@Component({
  changeDetection: ChangeDetectionStrategy.OnPush,
  selector: 'ae-navigation',
  templateUrl: './ae-navigation.component.html',
  styleUrls: ['./ae-navigation.component.scss'],
})
export class AeNavigationComponent {
  public style = this.navigationService.style$;
  public menu = this.navigationService.menu$;
  public toolbar = this.navigationService.toolbar$;

  menuIcon: IconType = 'menu';
  arrowLeft: IconType = 'keyboard_arrow_left';

  isHandset$: Observable<boolean> = this.breakpointObserver
    .observe(Breakpoints.Handset)
    .pipe(map((result) => result.matches));

  constructor(
    private breakpointObserver: BreakpointObserver,
    public navigationService: AeNavigationService
  ) {}

  handler(handlerFn: ((event: string) => void) | undefined, event: string) {
    if (handlerFn) handlerFn(event);
    return;
  }
}
