import { Component } from '@angular/core';
import { Router } from '@angular/router';
import { AeNavigationService } from 'projects/ae-navigation/src/lib';
import { AppRoutes } from './app.routes';
import { replace, toUpper } from 'lodash';
import { AeDocService } from 'projects/ae-doc/src/public-api';
@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss'],
})
export class AppComponent {
  title = 'Angular Library';

  constructor(
    private nav: AeNavigationService,
    private router: Router,
    private docService: AeDocService
  ) {
    // setting menu
    this.nav.menu(
      AppRoutes.map((e) => ({
        ...e,
        eventHandler: (event: string) => {
          if (event === 'click') {
            this.docService.next(e.data);
          }
        },
        label: toUpper(replace(e.path!, '-', ' ')),
      }))
    );
  }

  navigate(path: string) {
    this.router.navigate([path]);
  }
}
