import { Injectable } from '@angular/core';
import { IComponentService } from 'projects/ae-common/src/lib';
import { Subject, Subscription } from 'rxjs';
import { AeComponentView } from './ae-component-view.class';

@Injectable({
  providedIn: 'root',
})
export class AeComponentViewService
  implements IComponentService<AeComponentView> {
  public data$: Subject<Partial<AeComponentView>> = new Subject<
    Partial<AeComponentView>
  >();

  public snapshot: Partial<AeComponentView> = {};
  public subscriptions!: Subscription[];

  public next(view: Partial<AeComponentView>) {
    setTimeout(() => {
      this.data$.next(view);
    });
  }
}
